
'use server';
import { suggestProjectTags } from '@/ai/flows/suggest-project-tags';

export async function getSuggestedTags(projectContent: string): Promise<{ tags: string[], error?: string }> {
  if (!projectContent || projectContent.trim().length < 20) {
    return { tags: [], error: 'Please provide at least 20 characters of content for better suggestions.' };
  }
  try {
    const result = await suggestProjectTags({ projectContent });
    return { tags: result.tags };
  } catch (error) {
    console.error("AI tag suggestion failed:", error);
    return { tags: [], error: 'An unexpected error occurred while suggesting tags.' };
  }
}
