
"use client";

import { useState, useTransition } from 'react';
import { Textarea } from '@/components/ui/textarea';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Sparkles } from 'lucide-react';
import { getSuggestedTags } from '@/app/actions';
import { Alert, AlertDescription, AlertTitle } from './ui/alert';

export function AiTagger() {
  const [isPending, startTransition] = useTransition();
  const [projectContent, setProjectContent] = useState('');
  const [suggestedTags, setSuggestedTags] = useState<string[]>([]);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = () => {
    setError(null);
    startTransition(async () => {
      const result = await getSuggestedTags(projectContent);
      if (result.error) {
        setError(result.error);
        setSuggestedTags([]);
      } else {
        setSuggestedTags(result.tags);
      }
    });
  };

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <div className="flex items-center gap-3">
            <div className="bg-primary/10 p-2 rounded-lg">
                <Sparkles className="w-6 h-6 text-primary" />
            </div>
            <CardTitle className="font-headline text-2xl">AI Tag Suggester</CardTitle>
        </div>
        <CardDescription>
          Enter your project description, technologies, or any relevant text below. Our AI will suggest relevant tags to improve discoverability.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="grid w-full gap-4">
          <Textarea
            placeholder="e.g., A full-stack web application using React, Node.js, and PostgreSQL for managing personal finances..."
            value={projectContent}
            onChange={(e) => setProjectContent(e.target.value)}
            rows={6}
          />
          <Button onClick={handleSubmit} disabled={isPending || !projectContent}>
            {isPending ? 'Generating...' : 'Suggest Tags'}
          </Button>
          {error && (
             <Alert variant="destructive">
                <AlertTitle>Error</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
             </Alert>
          )}
          {suggestedTags.length > 0 && (
            <div className="flex flex-wrap gap-2 pt-4">
              <h4 className="text-sm font-medium w-full text-muted-foreground">Suggested Tags:</h4>
              {suggestedTags.map((tag) => (
                <Badge key={tag} variant="secondary">{tag}</Badge>
              ))}
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
